/* 기본 스타일 설정 */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

p {
    padding: 0 ;
    margin: 0;
}

ol, ul {
    list-style: none;
}

body {
    font-family: 'Arial', sans-serif;
    background-color: #f9f9f9;
}

section {
    padding: 50px 0;
}

.center {
    text-align: center;
}
.invitation-container {
    width: 100%;
    max-width: 500px;
    margin: 0 auto; 
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);

    height: auto;
}

.invitation-header {
    text-align: center;
    padding-bottom: 20px;
    border-bottom: 2px solid #f0f0f0;
    padding: 20px;
}

.invitation-header h1 {
    font-size: 1.8rem;
    color: #a7a7a7;
    margin-bottom: 10px;
}

.invitation-header p {
    font-size: 1.2rem;
    color: #333;
}

.invitation-details {
    margin: 20px 0;
}

.invitation-details h2 {
    font-size: 1.4rem;
    font-weight: bold;
    margin-bottom: 10px;
    color: #a7a7a7;
}

.details {
    font-size: 1rem;
    color: #666;
}
 

.bride,
.groom {
    width: 48%;
    text-align: center;
}

.bride h3,
.groom h3 {
    font-size: 1.4rem;
    color: #a7a7a7;
    margin-bottom: 10px;
}

.bride p,
.groom p {
    font-size: 1rem;
    color: #666;
}

.rsvp {
    text-align: center;
}

.rsvp-button {
    padding: 10px 20px;
    font-size: 1.2rem;
    background-color: #a7a7a7;
    color: white;
    border: none;
    border-radius: 25px;
    cursor: pointer;
    transition: background-color 0.3s;
}

.rsvp-button:hover {
    background-color: #494949;
}

.invitation-footer {
    text-align: center;
    font-size: 0.9rem;
    color: #888;
    margin-top: 20px;
}
  
button {
    transition: all 0.4s;
}
button:hover {
    background-color: #ebebeb;
}

/* 시작 */
