}
.bride-groom-secion {
    display: flex;
    justify-content: space-evenly;
    margin-bottom: 20px;
    width: 40%;
    margin: 30px auto;
}

.info p {
    margin: 0;
}

/* 초대문구 스타일 */

.invitation_wrap { 
    height: 230px;
    background-color: #eee; 

 }

 /* 서약문구 */
 .wedding_vows_wrap { 
    height: 400px; 
    text-align: center;
 } 

 .main_img_2 {
    width: 100%;
    height: 600px;
    background-color: #888;
    /* background-image: url('../assets/img/main_img.jpg');
    background-repeat: no-repeat;
    background-size: cover; */
} 


.contact {
    padding: 100px 0;
}

.contact button {
    border: none;
    padding: 5px 10px; 
    background-color: initial;
 }


 .gallery_wrap {
    margin-top: 50px;
 }


 .images_wrap {
    position: relative;
    padding: 20px;
    height: 560px;
 }

 
 .imgBox {
    position: absolute;
    background-color: #eaeaea;
    width: 45%;
    height: 270px;
    margin-top: 30px;
 }

 .imgBox:nth-child(1) {
    top: 0;
    left: 20px;
    background-color: #eaeaeada;
 }
 .imgBox:nth-child(2) {
    top: -20px;
    right: 20px;
    background-color: #cacacada;
 } 
 .imgBox:nth-child(3) {
    bottom: 0;
    left: 20px;
    background-color: #d4d4d4da;
 }
 .imgBox:nth-child(4) {
    bottom: 20px;
    right: 20px;
}



/* TImer box */

.dDayTimer {
    margin: 100px 0;
}
.dd_title {
    font-weight: 900;
    font-size: 18px;
}
.countdown-box .time {
    margin: 40px 0;
    font-size: 32px;
    color: #818181;
}

.days {
    font-size: 30px;
    font-weight: 800;
}

.countdown-box .time > span { 
    background-color: #e8e8e8;
    padding: 20px 14px;
  
}


/* Contact Section */

.contact_coma {
    display: inline-block;
    width: 7px;
    height: 7px;
    background-color: #5f5f5f;
    border-radius: 100px;
    vertical-align: 1px;
    margin-right: 5px;
    box-shadow: -1px 2px 6px #0000007d;
    background: linear-gradient(to right, #000000a6, #8080808f);
}

.dropdownUl {
    border: 1.5px solid #959595;
    width: 80%;
    margin: 0 auto;
    border-radius: 10px;
    padding: 10px;
}

.dropdownUl > li > span {
    display: inline-block;
    font-size: 10px;
    color: #000000;
    cursor: pointer;
    background: #ededed;
    padding: 3px 5px;
    vertical-align: 2px;
    /* height: 20px; */
    /* line-height: 13px; */
 }

 .contact .dot {
    display: inline-block;
    width: 8px;
    height: 8px;
    background-color: #cbcbcb;
    border-radius: 100px;
    vertical-align: 2px;
    margin-right: 10px;
 }



/* animation */

/* fade in */
@keyframes slide-fade-in-dropdown-animation {
    0% {
        transform: translateY(-100%);
    }
    100% {
        transform: translateY(0); 
    }
}

.slide-fade-in-dropdown {
    overflow: hidden;
}

.slide-fade-in-dropdown > ul {
    animation: slide-fade-in-dropdown-animation .4s ease;
}

/* fade out/ */

@keyframes slide-fade-out-dropdown-animation {
    0% {
        transform: translateY(0);
    }
    100% {
        transform: translateY(-100%);
    }
}

.slide-fade-out-dropdown {
    overflow: hidden;
}

.slide-fade-out-dropdown > ul {
    animation: slide-fade-out-dropdown-animation .4s ease;
    animation-fill-mode: forwards;
}

.components-dropdown > ul{
    list-style: none;
} 

.line_bar {
    display: inline-block;
    width: 75%;
    height: 1.8px;
    background-color: #000;
}

.contact button.contact_pop {
    transition: 0.3s;
    border: 1px solid #ffb0cc; 
    border-radius: 20px; 
    margin-top: 20px; 
    height: 40px;
    color: #ffb0cc;
    }
.contact button.contact_pop:hover {
   background-color: #ffcfe0;
   color: #fff;
   font-weight: 900;
   border: initial;
}

.contact_box {
    display: flex;
    gap: 10px;
}


/* ====================
button style (map ..)
==================== */

#map{
    margin: 0 auto;
}
.map_icons { 
    display: flex;
    justify-content: center;
    margin-top: 20px;
    gap: 15px;
}

.map_icons a > * {
    float: left;
    width: auto;
    color: #4b4b4b;
    font-size: 13px;
    line-height:2.5;
    margin: 0 2px;
    letter-spacing: -1px;
}

.map_icons a > img {
    width: 30px;
}
.map_btn , .call_btn {
    color: #aaa;
    text-decoration: none;
    border: 1px solid #aaa;
    border-radius: 50px;
    padding: 0;
    width: 80px;
    margin: 10px 5px;
    margin-top: 20px;
    height: 35px;
    line-height: 35px;
    display: inline-block;
}