 
table {
    /* border: 0.8px solid #aaa; */
}
.calendar {
    width: 100%;
    border-collapse: collapse;
    table-layout: fixed; 
}

tbody {
    border: 0.8px solid #aaa;
}

.calendar th, .calendar td {
    padding: 10px;
    text-align: center;
    border: 0.8px solid #aaa;
}

.calendar th {
    border: none; 
}

.calendar td {
    position: relative;
}

.inner {
    display: inline-block;
    font-size: 16px;
    width: 25px; 
}

.inner > span {
    position: absolute;
    top: 3px;
    left: 5px;
    font-size: 13px;
    font-weight: 900;
}

.holiday {
    color: red;
}

.today {
    background-color: #ffcc00;
    font-weight: bold;
}
.outDay {
    color: #aaa;
}
.special_day, .national_holiday, .lunar {
    font-size: 12px;
    color: #555;
}
.special_day {
    font-style: italic;
}


/* 
.weddingday {
    position: relative;
} */

.weddingday::after {
    position: absolute;
    content: '';
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 35px;
    height: 35px;
    border: 2px solid #0c0c0c;
    border-radius: 100px;
}

.weddingday::before {
    position: absolute;
    content: 'Wedding Day ♥ ';
    top: -24px;
    left: -35px;
    font-size: 9px;
    width: 73px;
    transform: rotate(-23deg);
    font-weight: 800;
    letter-spacing: -0.7px;
}