/* 기본 스타일링 */
.container {
    margin-top: 20px;
    height: 1000px;
    width: 100%; 
    min-width: 330px;
  }
  
  img {
    width: 100%;
    height: auto;
  }
  
  .gallery_wrap p {
    font-weight: bold;
  }
  .modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,0.8);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 100;
  }

  .modal-content {
    position: relative;
    max-width: 90%;
    max-height: 90%;
    background-color: #fff;
}

.modal-image {
    max-width: 100%;
    max-height: 100%;
}

.close-button {
    position: absolute;
    top: 10px;
    right: 10px;
    font-size: 30px;
    color: white;
    background-color: transparent;
    border: none;
    padding: 10px;
    cursor: pointer;
}

.prev-button,
.next-button {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    font-size: 30px;
    background: rgba(0,0,0,0.5);
    color: #fff;
    border: none;
    padding: 10px;
    cursor: pointer;
}

.prev-button {
    left: 10px;
}

.next-button {
    right: 10px;
}

.prev-button:hover,
.next-button:hover {
  background: rgba(170, 170, 170, 0.7);
}

.modal-overlay:hover {
    cursor: pointer;
  }


  /* 갤러리 위치 */

  .img_wrap_1, .img_wrap_2 {
